@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
.nav-style {
    background-color: white;
    border-bottom: 1px solid lightgrey;
}
body {
    --video-overly-text: rgb(99, 99, 99);
}
.videoContainer {
    position: relative;
    width: 100%;
    height: 100%;
    margin-top: -450px;
    background-attachment: scroll;
    overflow: hidden;
    max-width: 100%;
}       
.videoContainer video {
    /* min-width: 100%;
    min-height: 100%; */
    position: relative;
    z-index: 1;
}
.videoContainer .myGradient {
    height: 100%;
    /* width: 100%; */
    width: 15%;
    position: absolute;
    top: 0px;
    left: 30%;
    z-index: 2;
    /* opacity: 0.5; */
    background: -webkit-gradient(linear, left top, right top, from(black) , to(rgba(255, 255, 0, 0)));
    background: -webkit-linear-gradient(left, black , rgba(255, 255, 0, 0));
    background: linear-gradient(to right, black , rgba(255, 255, 0, 0));
    /* background: black; */
}
.banner {
    height: 100%;
    width: 30%;
    top: 0px;
    left: 0px;
    background-color: black; 
    position: absolute;
    z-index: 3;

}
video {
    width: 70%;
    min-width: 70%;
    left: 30%;
}
.videoTitle {
    font-family: 'Righteous', cursive;
    font-size: 4vw;
    color: var(--video-overly-text);
    z-index: 5;
}
.features {
    font-family: 'Righteous', cursive;
    font-size: 3vw;
    color: var(--video-overly-text);
    z-index: 5;
}
.visitProject {
    display: flex;
    
}
.jumbo-text {
    font-family: 'Righteous', cursive;
    font-size: 4vw; /* 80px */
}
.info-text {
    font-family: 'Righteous', cursive;
    font-size: 2vw; /* 20px */
}
.bg-img {
    position: fixed;
    height: 300px;
}
.start-btn {
    height: 75px;
    width: 75px;
}
.dark-btn {
    width: 45px;
    height: 45px;
    color: white;
}
.darkmode-text {
    color: palevioletred;
    font: bold;
}
.projectCard {
    width: 25rem;
}
.moon-btn {
    background-color: palevioletred;
}

.jumbo-text-phone {
    font-family: 'Righteous', cursive;
    font-size: 180px;
}
.jumbotron {
    height: 600px;
}
